<template>
  <div class="container">
    <v-card
    v-for="item in items" :key="item.memberId"
    class="mx-auto"
    max-width="1000"
  >
  <!-- 프로필 -->
    <div>
      <div class="flex">
    <v-avatar 
      class="ml-10 my-10 mr-4" 
      color="grey darken-1" 
      size="64"
      >
        <v-img
          aspect-ratio="30"
          src="data:image/jpeg;base64,"
        ></v-img>
      </v-avatar>
      <div class="user text-overline">{{item.memberId}}<br>{{item.writeDate | moment('YYYY-MM-DD HH:mm')}}</div>
      <!-- 수정, 삭제 버튼-->
      <v-spacer></v-spacer>
      <v-card-actions v-if="item.memberId == memberId" class="mr-15">
        <v-btn small outlined color="success" @click="updateBoard()">수정</v-btn>
        <v-btn small outlined color="error" @click="deleteBoard()">삭제</v-btn>
      </v-card-actions>
    </div>
    </div>
    

    <!-- 게시글 내용 -->
    <div class="contents">
    <v-card-text class="text--primary ml-16">
      <div v-html="content"></div>
    </v-card-text>
    </div>

    <BoardComment :onerId="item.memberId" :boardType="boardType"></BoardComment>
</v-card>
</div>
</template>
<script>
import BoardComment from "./BoardComment.vue";

export default {
    data() {
        return {
          moimId : this.$route.query.moimId,
          boardId : this.$route.query.boardId,
          boardType : this.$route.query.boardType,
          memberId : this.$store.state.id,
          title : this.$route.query.title,
          content : '',
            items: [],
            writer : 0,
        };
    },
    components: { BoardComment },
    created() {
      this.getBoard();
      this.getPost();
    },
    mounted(){
      console.log(this.boardId);
    },
    methods : {
      updateBoard() {
        this.$router.push({ name: "BoardUpdate" , query: { boardId: this.boardId, boardType : this.boardType, title : this.title } })
      },
      getPost() {
        this.axios.get("/readMoimInfo", {
          params : {
            boardId : this.boardId
          }
        })
        .then((resp)=> {
          console.log(resp)
          this.content = resp.data;
          console.log(content)
        })
        .catch((err) => {
          console.log(err)
        })
      },
      getBoard() {
      this.axios.get("/oneBoard", {
        params : {
          moimId : this.moimId,
          boardId : this.boardId,
          boardType : this.boardType
        }
      })
      .then((resp)=> {
        console.log(resp)
        console.log(this.items)
        this.items = resp.data;
      })
      .catch((err) => {
        console.log(this.items)
        console.log(err)
      })
      },
      deleteBoard() {
      this.$swal({
        title: '정말 삭제할까요?',
        text: "삭제를 원하지 않으면 취소버튼을 눌러주세요!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#2ac187',
        cancelButtonColor: '#d33',
        cancelButtonText: '취소',
        confirmButtonText: '네, 삭제할게요!'
      }).then((resp) => {
        if (resp.isConfirmed) {
          let vm = this;
       this.axios.delete("/deleteBoard", {
        params:{
          boardId : this.boardId,
          boardType : this.boardType
        }
      }).then((resp) => {
        console.log("게시글 삭제 결과" + resp);
        this.$swal(
            '삭제 완료!',
            '작성한 게시글을 삭제하였습니다.',
            'success'
          )
         vm.$router.push({ name: "moimBoard" })
      }).catch((err)=> {
        console.log(err)
      })
        }
      })
    }
    },
};
</script>
<style scoped>

.container{
  width : 85%;
  margin-top: 30px;
}

.user {
  display: inline-block;
  margin-left: 5px;
  margin-top: 40px;
}

.contents{
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 30px;
  margin-bottom: 50px;
}
 .flex {
  display : flex;
  height: 150px;
 }

 .hashtag{
  margin-left: 50px;
  margin-bottom: 40px;
 }

</style>